import { Component, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { BusFactory } from './bus';

export interface IAddress {
  line1: string;
  line2?: string;
  city: string;
  state: string;
  postalCode: string;
  country: string;
}

@Component({
  selector: 'architect-address-widget',
  template: `
    <h1>
      {{ title }}
    </h1>
    <div class='col-12'>
      <label>line 1:</label>
      <span *ngIf="address">{{ address.line1 }}</span>
    </div>
    <div class='col-12'>
      <label>line 2:</label>
      <span *ngIf="address">{{ address.line2 }}</span>
    </div>
    <div class='col-12 col-sm-5'>
      <label>city:</label>
      <span *ngIf="address">{{ address.city }}</span>
    </div>
    <div class='col-12 col-sm-3'>
      <label>state:</label>
      <span *ngIf="address">{{ address.state }}</span>
    </div>
    <div class='col-12 col-sm-4'>
      <label>postal code:</label>
      <span *ngIf="address">{{ address.postalCode }}</span>
    </div>
    <div class='col-12'>
      <label>country:</label>
      <span *ngIf="address">{{ address.country }}</span>
    </div>
  `,
  styles: [`

  `],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AppComponent {
  title: string;
  bus: any;
  address: IAddress;

  constructor(private ref: ChangeDetectorRef) {
    this.title = 'address';
    this.bus = BusFactory.create();
    this.bus.listen({
      eventName: 'architect-selected',
      responder: (event) => {
        this.address = this.getAddress(event.message.name);
        ref.detectChanges();
      }
    });
  }

  private getAddress(architect): IAddress {
    switch (architect) {
      case 'Seshan':
        return {
          line1: '123 Scenic Dr',
          line2: 'Apt 43',
          city: 'Nashville',
          state: 'TN',
          postalCode: '37203',
          country: 'US'
        };
      case 'Nijil':
        return {
          line1: '456 Pearl St',
          city: 'Nashville',
          state: 'TN',
          postalCode: '37203',
          country: 'US'
        };
      case 'Aneesh':
        return {
          line1: '789 Main St',
          city: 'Nashville',
          state: 'TN',
          postalCode: '37203',
          country: 'US'
        };
      default:
        return {
          line1: 'Corner of 65 and Charlotte',
          city: 'Nashville',
          state: 'TN',
          postalCode: '37203',
          country: 'US'
        };
    }
  }
}
